  color: inherit;
}

.hero-image {
  flex: 3 1;
  background-size: cover;
  background-position: center;
}

.content-grid {
  display: flex;
}

.col-left {
  flex: 1 1;
  width: 50%;
  margin-right: 20px;
  box-sizing: border-box;
}

.col-right {
  flex: 2 1;
  width: 50%;
  height: 100%;
  margin-left: 50px;
  box-sizing: border-box;
  background-color: #666666;
  background-color: #272727;
  background-color: var(--color-charcoal);
}




/***** Carousel Style ********/

.carousel {
  /* height: calc(var(--body-height) - 10vh); */
  /* min-height: 200px; */
  width: 100%;
  margin-top: 15vh;
  margin-bottom: 15vh;
}

.carousel img {
  height: auto;
  max-height: 70vh;
  max-width: 100%;
}

.thumbnail{
  height: 30px;
}

.carousel-arrow {
  font-size: 30px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
  background-color: #272727;
  background-color: var(--color-charcoal);
}

.carousel-arrow:hover {
  cursor: pointer;
}


.trip-content-details {
  padding: 40px 20px 40px 20px;
  width: 70%;
}

.trip-content-details img {
  width: 100%;
}




.map {
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  margin-top: 80px;
  margin-top: var(--nav-height);
  padding-bottom: 80px;
  padding-bottom: var(--nav-height);
  box-sizing: border-box;
  border-style: none;
}

.map > iframe {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

#footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  padding-top: 10px;
  padding-bottom: 10px;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  /* visibility: hidden; */
}





/****** Media Queries *********/

@media all and (max-width:820px) {
  :root {
    --side-margins: 0;
  }
  .hamburger {visibility: visible;}
  .menu {display:none;}
}

@media all and (max-width:768px) {

  .trip-hero {
    flex-direction: column;
  }
  .hero-text-container {
    padding: 30px;
    max-width: none;
  }
  .hero-text-container ul {
    margin-top: 15px;
  }
  .hero-text-container hr {
    display: none;
  }
  .hero-text-title h1 {
    margin-bottom: 20px;;
  }
  .hero-text {
    margin-top: 2px;
    margin-bottom: 2px;
  }



  .content-section {
    margin-left: 10vw;
    margin-right: 10vw;
    width: 80% !important;
  }
  .tiles {
    width: 100%;
    /* margin-top: 20px; */
    /* margin-bottom: 20px; */
    grid-gap: 3px !important;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  }
  .tile {
    height: 25vh !important;
  }

  .carousel {
    margin-top: 0;
    margin-bottom: 5vh;
  }

  #footer {
    height: 50px;
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

